// 던전 탐험 - 텍스트 기반 스타일 (무협 게임과 어울리게)

.dungeon-explorer-text {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  font-family: 'Noto Sans KR', 'Malgun Gothic', sans-serif;
  color: #2c2416;
  background: linear-gradient(135deg, #f5f1e8 0%, #e8dcc8 100%);
  border-radius: 6px;
  max-height: 70vh;
  overflow-y: auto;
  
  // 던전 상태 바
  .dungeon-status-bar {
    display: flex;
    gap: 1.5rem;
    padding: 0.8rem 1rem;
    background: rgba(139, 69, 19, 0.1);
    border-left: 3px solid #8b4513;
    font-size: 0.9rem;
    border-radius: 4px;
    
    .status-item {
      display: flex;
      gap: 0.3rem;
      
      .label {
        font-weight: 600;
        color: #8b4513;
      }
      
      .value {
        color: #2c2416;
        
        &.hp-value {
          color: #dc3545;
          font-weight: 600;
        }
      }
    }
  }
  
  // 방 정보
  .room-info-text {
    padding: 1rem;
    background: rgba(139, 69, 19, 0.05);
    border-radius: 4px;
    border: 1px solid rgba(139, 69, 19, 0.2);
    
    .room-title {
      font-size: 1.2rem;
      font-weight: 600;
      color: #8b4513;
      margin-bottom: 0.5rem;
      
      .room-status-badge {
        margin-left: 0.5rem;
        font-size: 0.85rem;
        padding: 0.2rem 0.6rem;
        background: #ffc107;
        color: #333;
        border-radius: 3px;
        
        &.cleared {
          background: #28a745;
          color: white;
        }
      }
    }
    
    .room-description {
      font-size: 0.95rem;
      line-height: 1.6;
      color: #4a3520;
    }
  }
  
  // 전투 로그
  .battle-log-section {
    padding: 1rem;
    background: rgba(220, 53, 69, 0.05);
    border-left: 3px solid #dc3545;
    border-radius: 4px;
    
    .battle-header-text {
      font-size: 1.1rem;
      font-weight: 600;
      color: #dc3545;
      margin-bottom: 1rem;
      text-align: center;
    }
    
    .monster-list-text {
      display: flex;
      flex-direction: column;
      gap: 0.8rem;
      margin-bottom: 1rem;
      
      .monster-item {
        padding: 0.8rem;
        background: rgba(255, 255, 255, 0.5);
        border-left: 3px solid #dc3545;
        border-radius: 4px;
        transition: all 0.2s;
        
        &.defeated {
          opacity: 0.5;
          border-left-color: #6c757d;
          background: rgba(108, 117, 125, 0.1);
        }
        
        .monster-name {
          font-size: 1rem;
          font-weight: 600;
          color: #dc3545;
          margin-bottom: 0.3rem;
          display: flex;
          align-items: center;
          justify-content: space-between;
          
          .monster-status {
            font-size: 0.9rem;
            
            &.defeated-mark {
              opacity: 0.7;
            }
          }
        }
        
        .monster-stats {
          font-size: 0.9rem;
          color: #666;
        }
        
        &.defeated .monster-name {
          color: #6c757d;
          text-decoration: line-through;
        }
      }
    }
    
    .battle-notice-text {
      text-align: center;
      font-size: 0.9rem;
      color: #666;
      font-style: italic;
      padding: 0.5rem;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 4px;
    }
  }
  
  // 보상
  .rewards-text-section {
    padding: 1rem;
    background: rgba(40, 167, 69, 0.1);
    border-left: 3px solid #28a745;
    border-radius: 4px;
    
    .rewards-header {
      font-size: 1.2rem;
      font-weight: 600;
      color: #28a745;
      margin-bottom: 1rem;
      text-align: center;
    }
    
    .rewards-list {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      margin-bottom: 1rem;
      
      .reward-line {
        font-size: 0.95rem;
        padding: 0.5rem;
        background: rgba(255, 255, 255, 0.5);
        border-radius: 3px;
        border-left: 2px solid #28a745;
      }
    }
  }
  
  // 방 클리어
  .room-cleared-text {
    padding: 1rem;
    
    .cleared-message {
      font-size: 1.1rem;
      font-weight: 600;
      color: #28a745;
      text-align: center;
      margin-bottom: 1.5rem;
      padding: 0.8rem;
      background: rgba(40, 167, 69, 0.1);
      border-radius: 4px;
    }
    
    .room-choices {
      margin-bottom: 1rem;
      
      .choices-title {
        font-size: 1rem;
        font-weight: 600;
        color: #8b4513;
        margin-bottom: 0.8rem;
      }
      
      .choice-buttons {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
      }
    }
  }
  
  // 텍스트 버튼
  .text-button {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.7rem 1rem;
    background: rgba(139, 69, 19, 0.1);
    border: 1px solid #8b4513;
    border-radius: 4px;
    color: #2c2416;
    font-size: 0.95rem;
    cursor: pointer;
    transition: all 0.2s;
    font-family: inherit;
    font-weight: 500;
    
    &:hover {
      background: rgba(139, 69, 19, 0.2);
      transform: translateX(3px);
      box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    }
    
    &:active {
      transform: translateX(1px);
    }
    
    &.choice {
      justify-content: flex-start;
      
      .choice-number {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        min-width: 24px;
        height: 24px;
        background: #8b4513;
        color: white;
        border-radius: 50%;
        font-size: 0.85rem;
        font-weight: 600;
        margin-right: 0.5rem;
        padding: 0 0.3rem;
      }
    }
    
    &.primary {
      background: #28a745;
      border-color: #28a745;
      color: white;
      font-weight: 600;
      justify-content: center;
      
      &:hover {
        background: #218838;
        border-color: #1e7e34;
      }
    }
    
    &.danger {
      background: #dc3545;
      border-color: #dc3545;
      color: white;
      font-weight: 600;
      justify-content: center;
      
      &:hover {
        background: #c82333;
        border-color: #bd2130;
        transform: none;
      }
    }
  }
  
  // 하단 버튼
  .dungeon-footer-text {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
    border-top: 1px solid rgba(139, 69, 19, 0.2);
  }
}
